<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md4>
        <v-card class="elevation-12">
          <v-toolbar dark color="primary">
            <v-toolbar-title></v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-card-text>
            <form>
              <v-select
                v-validate="'required'"
                :items="items"
                v-model="form.select"
                :error-messages="errors.collect('select')"
                label="()"
                data-vv-name="select"
                required
              ></v-select>
              <v-text-field
                v-validate="'required|max:10'"
                v-model="form.name"
                :counter="10"
                :error-messages="errors.collect('name')"
                label=""
                data-vv-name="name"
                required
              ></v-text-field>
              <v-text-field
                v-validate="'required|min:4|max:20'"
                v-model="form.id"
                :counter="20"
                :error-messages="errors.collect('id')"
                label=""
                data-vv-name="id"
                required
              ></v-text-field>
              <v-text-field
                v-validate="'required|min:6|max:40'"
                v-model="form.password"
                :counter="40"
                :error-messages="errors.collect('password')"
                label=""
                data-vv-name="password"
                required
                type="password"
              ></v-text-field>
              <v-text-field
                v-validate="'required|email'"
                v-model="form.email"
                :error-messages="errors.collect('email')"
                label="E-mail"
                data-vv-name="email"
                required
              ></v-text-field>
              <v-text-field
                v-validate="'required|min:8|max:12'"
                v-model="form.phone"
                :counter="10"
                :error-messages="errors.collect('phone')"
                label=""
                data-vv-name="phone"
                required
              ></v-text-field>
              <v-checkbox
                v-validate="'required'"
                v-model="checkbox"
                :error-messages="errors.collect('checkbox')"
                value="1"
                label=" ."
                data-vv-name="checkbox"
                type="checkbox"
                required
              ></v-checkbox>

              <v-btn @click="submit"></v-btn>
              <v-btn @click="clear"></v-btn>
            </form>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
    <v-snackbar
  v-model="sb.act"
>
  {{ sb.msg }}
  <v-btn
    :color="sb.color"
    flat
    @click="sb.act = false"
  >
    
  </v-btn>
</v-snackbar>
  </v-container>
</template>

<script>
import axios from 'axios'

export default {
  $_veeValidate: {
    validator: 'new'
  },

  data: () => ({
    form: {
      name: '',
      id: '',
      email: '',
      password: '',
      phone: '',
      type: ''
    },
      elect: null,
      items: [
        '',
        '()',
        '()',
        '()',
        '()'
      ],
    sb: {
        act: false,
        msg: '',
        color: 'warning'
    },
    checkbox: null,
    dictionary: {
      attributes: {
        email: 'E-mail Address'
        // custom attributes
      },
      custom: {
        name: {
          required: () => '  .',
          max: '  10    .'
          // custom messages
        },
        id: {
          required: () => '  .',
          min: ' 4 ~ 20    .',
          max: ' 4 ~ 20    .'
        },
        email: {
          required: () => '  .',
        },
        select: {
          required: () => ' .'
        },
        checkbox: {
          required: () => ' .'
        },
        password: {
          required: () => '  ',
          min: ' 6 ~ 40    .',
          max: ' 6 ~ 40    .'
        },
        phone: {
          required: () => '   ',
          min: '    .',
          max: '    .'
        }
      }
    }
  }),

  mounted () {
    this.$validator.localize('en', this.dictionary)
  },

  methods: {
    submit () {
      this.$validator.validateAll()
      .then(r => {
        if (!r) throw new Error(' ')
        return this.$axios.post('api/members', this.form)
      })
      .then(r => {
        if (!r.data.success) throw new Error(' .')
        this.pop('  .', 'success')

        this.$route.push('/member/login')
      })
      .catch(e => this.pop(e.message, 'warning'))
  },
  pop(m, cl) {
    this.sb.act = true
    this.sb.msg = m
    this.sb.color = cl
  },
  clear () {
    this.form.id = ''
    this.form.password = ''
    this.form.name = ''
    this.checkbox = null
    this.form.phone = ''
    this.form.email = ''
    this.$validator.reset()
  }
}
}
</script>



